// 登录
Mock.mock('/api/login', 'post', options => {
  // 本地个人数据以及个人详细数据
  let user = [
    {
      username: 'aaabbb7891',
      password: '123456',
      repassword: '123456',
      token: 'aaa',
      id: 230

    },
    {
      username: '78798',
      password: '123456',
      repassword: '123456',
      token: 'bbb',
      id: 240
    }
  ]
  let userinfo = [
    {
      id: 230,
      username: 'aaabbb7891',
      nickname: '',
      email: '',
      token: 'aaa',
      user_pic: null
    },
    {
      id: 240,
      username: '78798',
      nickname: '',
      email: '',
      token: 'bbb',
      user_pic: null
    }
  ]
  // 判断数据是否在本地有存储 未存储则存储
  if (window.localStorage.getItem('user') === null) {
    window.localStorage.setItem('user', JSON.stringify(user))
    window.localStorage.setItem('userinfo', JSON.stringify(userinfo))
  }
  // 获取本地数据
  user = JSON.parse(window.localStorage.getItem('user'))
  userinfo = JSON.parse(window.localStorage.getItem('userinfo'))
  // 解析请求数据
  const body = JSON.parse(options.body)
  const username = body.username
  const password = body.password
  // 调用获取用户信息函数,用于判断是否有此用户
  const checkUser = user.find(
    (item) => item.username === username && item.password === password
  )
  // 没有用户返回失败信息
  if (!checkUser) {
    return { code: 201, data: { message: '账号或者密码不正确' } }
  }
  // 判断本地是否有文章数据 新用户直接存储导入json
  if (window.localStorage.getItem('res') === null) {
    window.localStorage.setItem('res', JSON.stringify(res))
    window.localStorage.setItem('article', JSON.stringify(articlelist))
  }
  window.localStorage.setItem('token', JSON.stringify(checkUser.token))
  return {
    code: 0,
    message: '登录成功！',
    token: checkUser.token
  }
})

// 注册
Mock.mock('/api/reg', 'post', options => {
  // 判断本地是否有个人信息数据
  if (window.localStorage.getItem('user') === null) {
    window.localStorage.setItem('user', JSON.stringify(user))
    window.localStorage.setItem('userinfo', JSON.stringify(userinfo))
  }
  // 导入个人信息数据
  const userinfo = JSON.parse(window.localStorage.getItem('userinfo'))
  const user = JSON.parse(window.localStorage.getItem('user'))
  // 获取请求体携带的数据
  const body = JSON.parse(options.body)
  const username = body.username
  // 判断是否有重名
  const checkUser = user.find(
    (item) => item.username === username
  )
  // 添加个人token
  const length = user.length
  let token = String.fromCharCode(user[length - 1].token.charAt(0).charCodeAt() + 1)
  token = token.concat(token).concat(token)
  // 添加个人id
  const id = user[length - 1].id + 10
  // 新增user
  const result = {
    username: body.username,
    password: body.password,
    repassword: body.repassword,
    token: token,
    id: id
  }
  // 新增userinfo
  const result1 = {
    id: id,
    username: body.username,
    nickname: '',
    email: '',
    token: token,
    user_pic: null
  }
  // 没有用户返回失败信息
  if (checkUser) {
    return {
      code: 1,
      message: '用户名被占用，请更换其他用户名！'
    }
  }
  // 新增并存储
  userinfo.push(result1)
  user.push(result)
  window.localStorage.setItem('user', JSON.stringify(user))
  window.localStorage.setItem('userinfo', JSON.stringify(userinfo))
  return {
    code: 0,
    message: '注册成功！'
  }
})
// 获取个人信息
Mock.mock('/my/userinfo', 'patch', (options) => {
  const body = JSON.parse(options.body)
  // 这边由于存储token的时候没有存为json数据
  const token = body.token.replaceAll('"', '')
  const userinfo = JSON.parse(window.localStorage.getItem('userinfo'))
  // 通过个人token 定位到userinfo
  let index1 = 0
  userinfo.forEach((item, index, array) => {
    if (item.token === token) {
      index1 = index
    }
  })
  return {
    code: 0,
    message: '获取个人信息！',
    data: userinfo[index1]
  }
})
// 修改个人信息
Mock.mock('/my/userinfo', 'post', (options) => {
  const body = JSON.parse(options.body)
  const userinfo = JSON.parse(window.localStorage.getItem('userinfo'))
  // 通过个人id定位到userinfo
  let index1 = 0
  userinfo.forEach((item, index, array) => {
    if (item.id === body.id) {
      index1 = index
    }
  })
  // 修改个人信息
  userinfo[index1].email = body.email
  userinfo[index1].nickname = body.nickname
  window.localStorage.setItem('userinfo', JSON.stringify(userinfo))
  return {
    code: 0,
    message: '修改个人信息成功！'
  }
})
// 修改个人密码
Mock.mock('/my/updatepwd', 'post', (options) => {
  const user = JSON.parse(window.localStorage.getItem('user'))
  const body = JSON.parse(options.body)
  const token = body.token.replaceAll('"', '')
  // 通过个人token 定位到user
  let index1 = 0
  user.forEach((item, index, array) => {
    if (item.token === token) {
      index1 = index
    }
  })
  // 判断输入密码是否和原密码相同
  if (user[index1].password !== body.old_pwd) {
    return {
      code: 200,
      message: '原密码错误！'
    }
  }
  // 修改个人密码
  user[index1].password = body.new_pwd
  user[index1].repassword = body.re_pwd
  window.localStorage.setItem('user', JSON.stringify(user))
  return {
    code: 0,
    message: '修改个人信息成功！'
  }
})
