// 添加文章分类
Mock.mock('/my/cate/add', 'post', (options) => {
  // 导文章分类数据
  const catelist = JSON.parse(window.localStorage.getItem('catelist'))
  const body = JSON.parse(options.body)
  const length = catelist.length
  // 设置文章id
  const id = catelist[length - 1].id + 2
  const result = {
    id: id,
    cate_name: body.cate_name,
    cate_alias: body.cate_alias
  }
  // 存入文章分类
  catelist.push(result)
  window.localStorage.setItem('catelist', JSON.stringify(catelist))
  return {
    code: 0,
    message: '新增文章分类成功！'
  }
})
// 删除文章分类
Mock.mock('/my/cate/del', 'delete', (options) => {
  // 导文章分类数据
  const catelist = JSON.parse(window.localStorage.getItem('catelist'))
  const body = JSON.parse(options.body)
  let index1 = 0
  catelist.forEach((item, index, array) => {
    if (item.id === body.id) {
      index1 = index
    }
  })
  catelist.splice(index1, 1)
  // 存入文章分类
  window.localStorage.setItem('catelist', JSON.stringify(catelist))
  return {
    code: 0,
    message: '删除文章分类成功！'
  }
})
