// 获取文章列表
Mock.mock('/my/article/list', (options) => {
  const articlelist = JSON.parse(window.localStorage.getItem('article'))
  const body = JSON.parse(options.body)
  const cateid = body.cate_id
  // 如果没有cataid 表示为获取全部数据 否则获取cateid数据
  if (!cateid) {
    return {
      code: 0,
      message: '获取文章列表成功！',
      total: 0,
      data: articlelist
    }
  }
  console.log('aaaa', articlelist)
  // 筛选为cateid的数据
  const checkUser = articlelist.filter(p => {
    return String(p.cateid).indexOf(String(cateid)) !== -1
  })
  // 没有查询到返回失败信息
  if (!checkUser) {
    return { code: 201, data: { message: '未分类' } }
  }
  // 如果有返回成功信息
  console.log(checkUser)
  return {
    code: 0,
    message: '获取文章列表成功！',
    total: 0,
    data: checkUser
  }
})
