// 发表文章
Mock.mock('/my/article/add', 'post', (options) => {
  // 获取本地文章列表
  const articlelist = JSON.parse(window.localStorage.getItem('article'))
  // 导文章分类数据
  const catelist = JSON.parse(window.localStorage.getItem('catelist'))
  // 获取传递数据
  const body = options.body
  const title = body.get('title')
  const content = body.get('content')
  const coverimg = body.get('cover_img')
  const state = body.get('state')
  const cateid = body.get('cate_id')
  const res = JSON.parse(window.localStorage.getItem('res'))
  // 设置文章列表id
  const length = articlelist.length
  const id = articlelist[length - 1].id + 1
  const result = {
    id: id,
    title: title,
    content: content,
    cover_img: coverimg,
    state: state,
    cate_id: cateid
  }
  // 添加文章详细内容
  res.push(result)
  window.localStorage.setItem('res', JSON.stringify(res))
  // 添加时间属性
  const myDate = new Date()
  // 通过cateid来查找catename
  let index1 = 0

  catelist.forEach((item, index, array) => {
    if (String(item.id) === cateid) {
      index1 = index
    }
  })
  const name1 = catelist[index1].cate_name
  const result1 = {
    id: id,
    title: title,
    pub_date: myDate,
    state: state,
    cateid: cateid,
    cate_name: name1
  }
  articlelist.push(result1)
  window.localStorage.setItem('article', JSON.stringify(articlelist))
  return {
    code: 0,
    message: '添加文章详情成功！'
  }
})
// 删除文章
Mock.mock('/my/article/info', 'delete', (options) => {
  // 删除文章需要同时删除res和article
  const articlelist = JSON.parse(window.localStorage.getItem('article'))
  const body = JSON.parse(options.body)
  let index1 = 0
  let index2 = 0
  // 通过id来删除 article
  articlelist.forEach((item, index, array) => {
    if (item.id === body.id) {
      index1 = index
    }
  })
  articlelist.splice(index1, 1)
  const res = JSON.parse(window.localStorage.getItem('res'))
  // 通过id来删除res
  res.forEach((item, index, array) => {
    if (item.id === body.id) {
      index2 = index
    }
  })
  res.splice(index2, 1)
  window.localStorage.setItem('res', JSON.stringify(res))
  window.localStorage.setItem('article', JSON.stringify(articlelist))
  return {
    code: 0,
    message: '删除文章分类成功！'
  }
})
