import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
// 导入axios
import { getUserInfoAPI } from '@/api'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: '', // 用户token
    userInfo: {}, // 定义用户信息对象

    tabList: [
      {
        indexPath: '/',
        title: '首页'
      }

    ]

  },
  getters: {
    nickname: state => state.userInfo.nickname, // 昵称
    username: state => state.userInfo.username, // 用户名
    user_pic: state => state.userInfo.user_pic // 用户头像
  },
  mutations: {
    // 更新 token 的 mutation 函数
    updateToken (state, newToken) {
      state.token = newToken
    },
    // 更新用户的信息
    updateUserInfo (state, info) {
      state.userInfo = info
    },
    SELECTMENU (state, val) {
      // 判断添加的数据是否为首页
      if (val.name !== 'home') {
        const index = state.tabList.findIndex(item => item.title === val.title)
        // 当Index为-1时，表示tabList中没有，则可以添加面包屑
        if (index === -1) {
          state.tabList.push(val)
        }
      }
    }
  },
  actions: {
    // 定义初始化用户基本信息的 action 函数
    async initUserInfo (store) {
      console.log('mmm', localStorage.getItem('token'))
      const { data: res } = await getUserInfoAPI({ token: localStorage.getItem('token') })

      if (res.code === 0) {
        store.commit('updateUserInfo', res.data)
      }
    }
  },
  // 配置为 vuex 的插件
  plugins: [createPersistedState()]
})
